import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomePage} from './home.page';

const routes: Routes = [
  {
    path: '',
    component: HomePage,
    children: [
      {
        path: 'listing',
        loadChildren: () =>
          import('../../command/screens/listing/listing.module').then(
            (m) => m.ListingPageModule
          ),
      },
      {
        path: 'history',
        loadChildren: () =>
          import('../../command/screens/history/history.module').then(
            (m) => m.HistoryModule
          ),
      },
      {
        path: 'history/details/:id',
        loadChildren: () =>
          import('../screens/detailscommand/detailscommand.module').then((m) => m.DetailscommandModule),
      },

      {
        path: '**',
        redirectTo: '/command/listing',
        pathMatch: 'full'
      }

    ],
  },
  {
    path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomePageRoutingModule {
}
